			})
		},
			
		//报修列表
		getRepairList(status){
			this.$ajax('ServiceOrders',{catalog:-1,status:status},res=>{
				this.repairList=res
			})
			
		
		},
		
	},
	onLoad(){
		
		this.getRepairList(this.$store.state.repairStatus.untreated);
